import logo from '../../assets/svg/logo-name.svg'

export const Header = () => (
    <header className="header d-flex my-5 mx-6 align-center justify-content-between">
        <div className="logo">
            <img src={logo} alt="logo" />
        </div>
        <nav className="header-nav py-3">
            <ul className="header-nav-list d-flex justify-content-end col-gap-2">
                <li className="li-none-list-style">
                    <button>
                        Check Rad Gallery
                    </button>
                </li>
                <li className="li-none-list-style">
                    <button>
                        Visit Marketplace
                    </button>
                </li>
            </ul>
        </nav>
    </header>
   
)