import img1 from '../../assets/img/vote.png'
import vector from "../../assets/svg/Vector 3.svg";

export const Location = () => {


    return (
        <>
            <div className="location text-center">
                <div className="top">
                    <img src={img1} alt="vote" />
                </div>
                <p className="middle d-flex justify-content-center col-gap-2 align-center"> 
                    {/* <span> */}
                        <img class="rotate-180deg" src={vector} alt="vote" />
                    {/* </span> */}
                    <span>
                        OR
                    </span>
                    {/* <span> */}
                        <img src={vector} alt="vote" />
                    {/* </span> */}
                </p>
                <p className="bottom">
                    Select Existing Adventure
                </p>
            </div>
        </>
    )
}